:root {
  --checkout-max-width: 1100px; }

.Checkout {
  padding-bottom: var(--header-nav-height); }
  @media (min-width: 811px) {
    .Checkout {
      margin-top: var(--header-total-height); } }
  @media (max-width: 810px) {
    .Checkout {
      padding-bottom: calc(var(--navigation-tabs-height) + 113px); } }
  @media (min-width: 811px) {
    .Checkout::before {
      content: '';
      background: var(--breadcrumbs-background);
      min-height: var(--breadcrumbs-height);
      display: block; } }
  @media (max-width: 810px) {
    .Checkout-StickyButtonWrapper {
      border-top: 1px solid var(--primary-divider-color);
      background-color: #fff;
      position: fixed;
      padding: 1.2rem 1.2rem calc(env(safe-area-inset-bottom) + 1.2rem);
      bottom: 0;
      left: 0;
      width: 100%;
      z-index: 85; } }
  @media (max-width: 810px) {
    .Checkout-StickyButtonWrapper {
      background-color: var(--secondary-base-color);
      padding: 1.4rem 1.4rem calc(env(safe-area-inset-bottom) + 1.4rem); } }
  @media (max-width: 810px) {
    .Checkout-StickyButtonWrapper .Button {
      width: 100%; } }
  .Checkout-Wrapper {
    max-width: var(--checkout-max-width); }
    @media (min-width: 811px) {
      .Checkout-Wrapper {
        display: grid;
        grid-template-columns: repeat(2, minmax(calc(50% - 1.2rem), 470px));
        grid-gap: 2.4rem; } }
  .Checkout-Heading {
    text-transform: none;
    font-weight: 400;
    font-size: 2rem;
    margin: 0 0 1.2rem; }
    @media (max-width: 810px) {
      .Checkout-Heading {
        margin: 0 0 1.4rem; } }
  .Checkout-Title {
    text-transform: capitalize;
    font-weight: 600;
    font-size: 2.5rem;
    margin: 2.4rem 0; }
    @media (max-width: 810px) {
      .Checkout-Title {
        margin: 2.8rem 0;
        display: none; } }
  @media (min-width: 811px) {
    .Checkout-Coupon {
      margin-top: 30px; } }
  .Checkout-OrderTotal {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.8rem;
    font-weight: bold;
    padding-bottom: 1.4rem; }
    @media (min-width: 811px) {
      .Checkout-OrderTotal {
        display: none; } }
  .Checkout .Checkout-ExpandableContentContent {
    margin-top: 0; }
